.tui-pagination {
  margin-top: 50px;
  margin-bottom: 50px;
  text-align: center;
  font-size: 12px;
  font-weight: 500;
  color: var(--black-color);
  padding-bottom: 60px;
  @media screen and (max-width: 320px) {
    margin-top: 40px;
  }
}
.tui-hidden {
  display: none;
}
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellip,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination .tui-page-btn {
  display: inline-flex;
  justify-content: space-evenly;
  align-items: flex-end;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 12px;
  margin-left: 0;
  border-radius: 5px;
  vertical-align: middle;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: var(--white-color);
  background: var(--second-color);
  border-color: var(--second-color);
  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  background-color: var(--second-color);
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first-child.tui-is-selected {
  border-left: 1px solid var(--second-color);
}

.tui-pagination .tui-last-child.tui-is-selected {
  border-right: 1px solid var(--second-color);
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  align-items: center;
  overflow: hidden;
  white-space: nowrap;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-flex;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next {
  width: 16px;
  height: 16px;
  background: url('../images/arrow-left.svg');
  font-size: 0px;
}

.tui-pagination .tui-ico-next {
  transform: rotate(180deg);
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 6px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled {
  display: none;
}

.tui-page-inputs {
  display: inline-flex;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-flex;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  text-align: center;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: var(--second-color);
}

.tui-page-separator {
  display: inline-flex;
  padding: 8px 0 6px;
  vertical-align: top;
}
.tui-pagination .tui-last,
.tui-pagination .tui-first,
.tui-pagination .tui-first-child,
.tui-pagination .tui-last-child {
  @media screen and (max-width: 320px) {
    display: none;
  }
}
