.fire-logo {
  text-shadow: 0 3px 20px red, 0 0 20px red, 0 0 10px orange, 4px -5px 6px yellow,
    -4px -10px 10px yellow, 0 -10px 30px yellow;
  animation: 2s Blazing infinite alternate linear;
}

@keyframes Blazing {
  0% {
    text-shadow: 0 3px 20px red, 0 0 20px red, 0 0 10px orange, 0 0 0 yellow, 0 0 5px yellow,
      -2px -5px 5px yellow, 4px -10px 10px yellow;
  }
  25% {
    text-shadow: 0 3px 20px red, 0 0 30px red, 0 0 20px orange, 0 0 5px yellow, -2px -5px 5px yellow,
      3px -10px 10px yellow, -4px -15px 20px yellow;
  }
  50% {
    text-shadow: 0 3px 20px red, 0 0 20px red, 0 -5px 10px orange, -2px -5px 5px yellow,
      3px -10px 10px yellow, -4px -15px 20px yellow, 2px -20px 30px rgba(255, 255, 0, 0.5);
  }
  75% {
    text-shadow: 0 3px 20px red, 0 0 20px red, 0 -5px 10px orange, 3px -5px 5px yellow,
      -4px -10px 10px yellow, 2px -20px 30px rgba(255, 255, 0, 0.5),
      0px -25px 40px rgba(255, 255, 0, 0);
  }
  100% {
    text-shadow: 0 3px 20px red, 0 0 20px red, 0 0 10px orange, 0 0 0 yellow, 0 0 5px yellow,
      -2px -5px 5px yellow, 4px -10px 10px yellow;
  }
}

.logo__img {
  width: 24px;
  height: 24px;
  display: inline-block;
  margin: 0px;
  margin-right: 10px;
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  // padding-left: 71px;
  // padding-right: 71px;
  // padding-top: 41px;
  padding-top: 46px;
  padding-bottom: 41px;
}

.navigation__list {
  align-items: baseline;
  display: flex;
  padding: 0;
}

.navigation__item {
  position: relative;
  cursor: pointer;
  &:hover,
  &:focus {
    &::before {
      margin-top: 18px;
      content: '';
      height: 3px;
      width: 100%;
      position: absolute;
      background: var(--second-color);
      display: inline-block;
    }
  }
  &:first-child {
    margin-right: 39px;
  }
}
.navigation__item--current {
  &::before {
    margin-top: 18px;
    content: '';
    height: 3px;
    width: 100%;
    position: absolute;
    background: var(--second-color);
    display: inline-block;
  }
}

.navigation__link {
  color: var(--white-color);
  font-size: 12px;
  margin-right: 0;
  cursor: pointer;
  border: 0;
}

.search-form {
  position: relative;
  // margin-top: 37px;
  margin-bottom: 22px;
  display: inline-block;
  position: relative;
  width: 336px;
  height: 20px;
  &::before {
    position: absolute;
    left: 0;
    margin-top: 20px;
    content: '';
    height: 1px;
    width: 100%;
    background: var(--white-color);
    display: inline-block;
  }
  @media screen and (max-width: 425px) {
    width: 280px;
  }
}

.search-input {
  position: absolute;
  left: 0;
  width: 336px;
  height: 20px;
  background-color: transparent;
  border: 0px;
  margin-right: 217px;
  color: var(--white-color);
  font-size: 14px;
  font-weight: 100;
  &::placeholder {
    color: var(--white-color);
    font-size: 14px;
    font-weight: 100;
  }
  @media screen and (max-width: 425px) {
    width: 280px;
  }
}

.search-button {
  position: absolute;
  top: 4px;
  right: 4px;
  background-color: transparent;
  border: 0;
  cursor: pointer;
}

.watched-queue__button {
  border: none;
  color: var(--white-color);
  font-size: 12px;
  margin-right: 0;
  border: solid var(--white-color) 1px;
  width: 136px;
  height: 44px;
  border-radius: 5px;
  cursor: pointer;
  &:hover {
    color: var(--white-color);
    background-color: var(--second-color);
    filter: drop-shadow(0px 8px 43px rgba(255, 107, 1, 0.6));
    cursor: pointer;
    border: solid var(--white-color) 1px;
  }

  @media screen and (max-width: 767px) {
    width: 130px;
  }

  &--active {
    color: var(--second-color);
    border: solid var(--second-color) 2px;
  }
}

#watched-btn {
  margin-right: 15px;
}

.notification {
  opacity: 0;
  transition: 2s;
}

.notification.active {
  color: #ff001b;
  font-family: Roboto;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  display: block;
  opacity: 1;
  transition: 2s;
}
