.container {
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: 320px) {
    max-width: 320px;
    padding: 0px 20px 20px;
  }
  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding: 0px 75px 0px;
  }
  @media screen and (min-width: 1024px) {
    max-width: 1024px;
    padding: 0px 70px 0px;
  }
}
